<template>
  <web-view :src="url"></web-view>
</template>

<script>
export default {
  data() {
    return {
      url: '',
    }
  },
  onLoad(options) {
    if (options.title) {
      uni.setNavigationBarTitle({
        title: options.title,
      })
    }
    let url = decodeURIComponent(options.url)
    this.url = url
  },
  methods: {},
}
</script>

<style></style>
